<template>
    <div class="login-container-layout">
        <div class="login-container">
            <p class="login-title">회원유형선택</p>
            <div class="button-checkbox">
                <div :class="'item '+indiActive" @click="setRole('indi')">
                    <img :src="indiImg">
                    <div>개인회원</div>
                </div>
                <div :class="'item '+busiActive" @click="setRole('busi')">
                    <img :src="busiImg">
                    <div>기업회원</div>
                </div>
            </div>
            <v-btn type="button" class="blackButton" @click="next()">
                <span>다음</span>
            </v-btn>
        </div>
    </div>
</template>
<script>
    import AUTH from '@/config/web/auth'

    export default {
        name: 'joinIntro',
        data() {
            return {
                role: 'indi',
                indiActive: 'active',
                busiActive: '',
                indiImg: require('@/assets/img/ico_join1_a.svg'),
                busiImg: require('@/assets/img/ico_join2.svg'),
            };
        },
        created() {
            const member = AUTH.getSession();
            if (member && member?.memberInfo) {
                this.$router.push('/');
                return;
            }
        },
        methods: {
            setRole(role) {
                this.role = role
                if (role == 'indi') {
                    this.indiActive = 'active';
                    this.busiActive = '';
                    this.indiImg = require('@/assets/img/ico_join1_a.svg')
                    this.busiImg = require('@/assets/img/ico_join2.svg')
                } else {
                    this.indiActive = '';
                    this.busiActive = 'active';
                    this.indiImg = require('@/assets/img/ico_join1.svg')
                    this.busiImg = require('@/assets/img/ico_join2_a.svg')
                }
            },
            next() {
                if (!this.role) {
                    let params = {
                        message: '회원유형을 선택하세요',
                    };
                    this.$eventBus.$emit('modalOpen', params);
                    return;
                }
                sessionStorage.setItem('joinRole', this.role)
                this.$router.push('/memberJoin');

            }
        }

    }
</script>


<style lang="scss" scoped>
    .login-container {
        width: 636px;
        margin: 0 auto 430px auto;
        padding: 36px 24px 50px 24px;
        background: #fff;

        .login-title {
            color: #333;
            font-size: 24px;
            font-weight: bold;
            margin-bottom: 55px;
            padding-bottom: 8px;
            border-bottom: solid 1px #333;
        }


        .button-checkbox {
            width: 100%;
            margin: 0 auto 35px auto;
            display: flex;
            justify-content: space-between;

            .item {
                width: 282px;
                height: 120px;
                border: solid 1px #ddd;
                text-align: center;
                border-radius: 5px;
                padding-top: 20px;
                color: #DDDDDD;
                cursor: pointer;
                font-size: 14px;

                &.active {
                    border: solid 3px #333;
                    color: #333;
                    font-weight: bold;

                }
            }
        }
    }

    @media screen and (min-width: 450px) and (max-width: 1259px) {
        .login-container-layout {
            min-height:calc(100vh - 50px);
            .login-container {
                width: 90%;
                margin: 40px auto 0 auto;
                .button-checkbox {
                    .item {
                        width: 50%;

                        &:first-child {
                            margin-right: 24px;
                        }
                    }
                }
            }
        }

    }

    @media screen and (max-width: 450px) {
        .login-container-layout {
            .login-container {
                width: 100%;
                margin: 0;
                padding: 36px 20px;
                .button-checkbox {
                    .item {
                        width: 50%;

                        &:first-child {
                            margin-right: 24px;
                        }
                    }
                }
            }
        }
    }
</style>